// src/jsx/pages/HomePage.js
import React from 'react';
import { Link } from 'react-router-dom';
import logo from '../../images/logo.png'; // Update the path to your logo image

const HomePage = () => {
  return (
    <div className="homepage">
      <header style={headerStyle}>
        <div style={logoContainerStyle}>
          <img src={logo} alt="Logo" style={logoStyle} />
        </div>
        <nav style={navStyle}>
          <Link to="/login" style={linkStyle}>Login</Link>
          <Link to="/register" style={linkStyle}>Register</Link>
        </nav>
      </header>
      <main style={mainStyle}>
        <h1>Welcome to GetSkills</h1>
        <p>Your journey to acquiring new skills starts here.</p>
      </main>
    </div>
  );
};

// Inline styles (you can alternatively use a CSS/SCSS file)
const headerStyle = {
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  padding: '15px 20px',
  borderBottom: '1px solid #ddd',
  backgroundColor: '#fff'
};

const logoContainerStyle = {
  flex: '1'
};

const logoStyle = {
  height: '50px'
};

const navStyle = {
  display: 'flex',
  gap: '20px',
  alignItems: 'center'
};

const linkStyle = {
  textDecoration: 'none',
  color: '#007bff',
  fontSize: '16px',
  fontWeight: 'bold'
};

const mainStyle = {
  padding: '40px 20px',
  textAlign: 'center'
};

export default HomePage;
